}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.slider__toggles {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, 12px);
  gap: 13px;
  align-items: center;
}
.slider__toggle {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  cursor: pointer;
  position: relative;
  width: 12px;
  height: 12px;
  background-color: inherit;
  border-radius: 50%;
}
.slider__toggle::before {
  position: absolute;
  width: 6px;
  height: 6px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  content: "";
  background-color: rgba(40, 54, 69, 0.3);
  border-radius: 50%;
}
.slider__toggle--current {
  background-color: #283645;
}
.slider__toggle--current::before {
  background-color: #ffffff;
}
.slider__toggle:hover {
  opacity: 0.6;
}
.slider__toggle:focus-visible::before {
  background-color: #283645;
}
.slider__toggle:focus {
  outline: none;
}
.slider__toggle:active {
  opacity: 0.1;
}
.slider__control {
  display: block;
  box-sizing: border-box;
  border: none;
  padding: 0;
  text-decoration: none;
  text-align: center;
  font: inherit;
  color: inherit;
  background-color: inherit;
  cursor: pointer;
  width: 22px;
  height: 41px;
  background-image: url("../../img/arrow.svg");
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0.3;
}
.slider__control--next {
  transform: rotate(180deg);
}
.slider__control:hover {
  opacity: 0.6;
}
.slider__control:focus-visible {
  outline: 2px solid #283645;
  outline-offset: 2px;
}
.slider__control:active {
  opacity: 0.1;
}
