.control__input[type="text"],
.control__input[type="textarea"] {
  border: 2px solid #E5E5E5;
  border-radius: 3px;
}
.control__input[type="text"]:hover,
.control__input[type="textarea"]:hover {
  border-color: #283645;
}
.control__input[type="text"]:focus,
.control__input[type="textarea"]:focus {
  border-color: #D22856;
  outline: none;
}
.control__mark {
  box-sizing: border-box;
  border: 2px solid #E5E5E5;
  border-radius: 3px;
}
.control:hover > .control__mark {
  border-color: #283645;
}
.control:focus > .control__mark {
  border-color: #D22856;
}
.control:active > .control__mark {
  border-color: #D22856;
}
.control__input[type="checkbox"] + .control__mark {
  width: 39px;
  height: 39px;
}
.control__input[type="radio"] + .control__mark {
  position: relative;
  width: 41px;
  height: 41px;
  border-radius: 50%;
}
.control__input[type="checkbox"]:checked + .control__mark {
  background-image: url("../../img/checkbox-tick.svg");
  background-repeat: no-repeat;
  background-size: 22px 16px;
  background-position: center;
}
.control__input[type="radio"]:checked + .control__mark::before {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 15px;
  height: 15px;
  margin: auto;
  background-color: #D22856;
  border-radius: 50%;
  content: "";
}
.modal {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}
.modal__container {
  position: relative;
  background-color: transparent;
}
